import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import Image from "../components/image"
import SEO from "../components/seo"
import react from "../images/react.png"
import node from "../images/node.png"
import Redux from "../images/Redux.png"
import typescript from "../images/typescript.png"
import mob from "../images/mob.png"
import web from "../images/web-app.png"
import mobile from "../images/mobile.svg"
import browser from "../images/browser.svg"

const Portfolio = () => (
  <>
    <div className="portfolio">
      <h2> Our craft</h2>
      <div className="works">
        <div className="mobile-app">
          <h4>
            {" "}
            <a href="/">Mobile app</a>
          </h4>

          <img src={mob} />
        </div>
        <div className="web-app">
          <h4>
            {" "}
            <a href="/">Web app</a>
          </h4>
          <img src={web} />
        </div>
      </div>
    </div>
  </>
)

const IndexPage = () => (
  <Layout>
    <SEO title="Home" />
    <div className="background-img">
      <img style={{ width: "280px", marginLeft: "-50px" }} src={browser} />
      <img style={{ width: "120px" }} src={mobile} />
    </div>
    <div style={{ zIndex: 3, position: "relative ", maxWidth: 500 }}>
      <h1>We create mobile and web apps</h1>
      <p>Increased speed and good user experience for your business</p>
    </div>
    <Portfolio/>
    {/* <div className="stack-tech">
      <div className="tech">
        <img src={react} />
      </div>
      <div className="tech">
        <img src={node} />
      </div>
      <div className="tech">
        <img src={Redux} />
      </div>
      <div className="tech">
        <img src={typescript} />
      </div>
    </div> */}

    <div style={{ maxWidth: `300px`, marginBottom: `1.45rem` }}></div>
  </Layout>
)

export default IndexPage
